<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap"></div>
      <fdcform ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="100">
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="" prop="code">
              <fdcinput v-model="formValidate.code" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="" prop="name">
              <fdcinput v-model="formValidate.name" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="" prop="module">
              <fdcselect v-model="formValidate.type">
                <fdcoption v-for="item in enumSelect['CARRIER_TYPE']" :value="item.enumCode" :key="item.enumCode">
                  {{ item.enumValue }}
                </fdcoption>
              </fdcselect>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="" prop="contact">
              <fdcinput v-model="formValidate.contact" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="" prop="telephone">
              <fdcinput v-model="formValidate.telephone" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="" prop="email">
              <fdcinput v-model="formValidate.email" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="15">
            <formitem label="" prop="remark">
              <fdcinput v-model="formValidate.remark" type="textarea" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7"></fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem>
              <fdcbutton type="primary" @click="handlerSubmit('formValidate')"></fdcbutton>
              <fdcbutton type="ghost" @click="cancel('addCarrier')" class="buttonClass"></fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
  </div>
</template>
<script>
import CarrierAPI from './api/CarrierAPI'
import {mapState} from 'vuex'
import Event from '@/utility/Event.js'
export default {
  name: 'addCarrier',
  data: function () {
    return {
      ruleValidate: {
        name: [{
          required: true,
          type: 'string',
          message: '',
          trigger: 'blur'
        }],
        code: [{
          required: true,
          type: 'string',
          message: '',
          trigger: 'blur'
        }]
      },
      formValidate: {

      }
    }
  },
  computed: {
    ...mapState({
      enumSelect: state => state.Enum.enumSelect
    })
  },
  methods: {
    insert: function () {
      CarrierAPI.insert(this.formValidate).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '',
            desc: ''
          })
          Event.$emit('closePage', 'addCarrier')
          Event.$emit('refreshCarrierList', null)
        } else {
          this.$Notice.error({
            title: '',
            desc: res.data.msg
          })
        }
      })
    },
    handlerSubmit: function (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.insert()
        }
      })
    },
    cancel: function (router) {
      Event.$emit('closePage', router)
    }
  },
  mounted: function () {
    const condition = ['CARRIER_TYPE']
    Event.$emit('addEnum', {typeList: condition})
  }
}
</script>



