<template>
  <div>
    <div class="tab-form">
      <tabs :value="tabName">
        <tabpane label="" name="userInfo">
          <div>
            <row>
              <fdccol span='10'>
                <div class="tab-form-avatar">
                  <img src="https://i.loli.net/2017/08/21/599a521472424.jpg"/>
                  <Upload ref="upload" :show-upload-list="false" :on-success="handleSuccess"
                          :format="['jpg','jpeg','png']"
                          :max-size="2048" :on-format-error="handleFormatError" :on-exceeded-size="handleMaxSize"
                          :before-upload="handleBeforeUpload" type="drag" :action="url"
                          class="user-upload">
                    <div class="upload-icon">
                      <Icon type="camera" size="20"></Icon>
                    </div>
                  </Upload>
                </div>
              </fdccol>

              <fdccol span='14'>
                <fdcform ref="formData" :model="formData" :label-width="100" :rules="ruleValidate">
                  <row>
                    <fdccol span='16'>
                      <formitem label="" prop="loginName">
                        <fdcinput v-model="formData.loginName" placeholder="">
                        </fdcinput>
                      </formitem>
                    </fdccol>
                  </row>
                  <row>
                    <fdccol span='16'>
                      <formitem label="" prop="name">
                        <fdcinput v-model="formData.name" placeholder="">
                        </fdcinput>
                      </formitem>
                    </fdccol>
                  </row>
                  <row>
                    <fdccol span='16'>
                      <formitem label="" prop="email">
                        <fdcinput v-model="formData.email" placeholder="">
                        </fdcinput>
                      </formitem>
                    </fdccol>
                  </row>
                  <row>
                    <fdccol span='16'>
                      <formitem label="" prop="contactInfo">
                        <fdcinput v-model="formData.contactInfo" placeholder="">
                        </fdcinput>
                      </formitem>
                    </fdccol>
                  </row>
                  <row>
                    <fdccol span='16'>
                      <formitem label="" prop="department">
                        <fdcinput v-model="formData.department" placeholder="">
                        </fdcinput>
                      </formitem>
                    </fdccol>
                  </row>
                  <row>
                    <fdccol span='16'>
                      <formitem label="" prop="idCard">
                        <fdcinput v-model="formData.idCard" placeholder="">
                        </fdcinput>
                      </formitem>
                    </fdccol>
                  </row>
                  <row type="flex" justify="space-around">
                    <fdccol span='24'>
                      <formitem>
                        <fdcbutton type="primary" @click="handleSubmit('formData')"></fdcbutton>
                        <fdcbutton type="ghost" @click="close" class="buttonClass"></fdcbutton>
                      </formitem>
                    </fdccol>
                  </row>
                </fdcform>
              </fdccol>
            </row>
          </div>
        </tabpane>

        <tabpane label="" name="modifyPassword">
          <div>
            <fdcform ref="formData2" :model="formData2" :rules="ruleValidate2" :label-width="80">
              <row type="flex" justify="space-around">
                <fdccol span="12">
                  <formitem label="" prop="oldPassword">
                    <fdcinput v-model="formData2.oldPassword" type="password" placeholder="Enter old password">
                    </fdcinput>
                  </formitem>
                </fdccol>
              </row>
              <row type="flex" justify="space-around">
                <fdccol span="12">
                  <formitem label="" prop="newPassword">
                    <fdcinput v-model="formData2.newPassword" type="password" placeholder="Enter new password">
                    </fdcinput>
                  </formitem>
                </fdccol>
              </row>
              <row type="flex" justify="space-around">
                <fdccol span="12">
                  <formitem label="" prop="confirmPassword">
                    <fdcinput v-model="formData2.confirmPassword" type="password" placeholder="Confirm password">
                    </fdcinput>
                  </formitem>
                </fdccol>
              </row>
              <row type="flex" justify="space-around">
                <fdccol span='12'>
                  <formitem>
                    <fdcbutton type="primary" @click="handleSubmit2('formData2')"></fdcbutton>
                    <fdcbutton type="ghost" @click="close" class="buttonClass"></fdcbutton>
                  </formitem>
                </fdccol>
              </row>
            </fdcform>
          </div>
        </tabpane>
      </tabs>
    </div>
  </div>
</template>

<script>
  import tags from '@/components/tags.vue'
  import UserAPI from './api/UserAPI'
  import Event from '@/utility/Event.js'
  import {mapState} from 'vuex'

  export default {
    name: 'userInfo',
    data() {
      return {
        tabName: 'userInfo',
        url: '',
        formData: {
          id: '',
          loginName: '',
          name: '',
          email: '',
          contactInfo: '',
          department: '',
          idCard: ''
        },
        ruleValidate: {
          loginName: [{
            required: true,
            message: '',
            trigger: 'blur'
          }, {
            type: 'string',
            message: '',
            trigger: 'blur'
          }],
          name: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          email: [{
            type: 'email',
            message: '',
            trigger: 'blur'
          }],
          contactInfo: [{
            type: 'string',
            trigger: 'blur'
          }],
          department: [{
            type: 'string',
            trigger: 'blur'
          }],
          idCard: [{
            required: true,
            pattern: /^[0-9]{15,18}$/,
            message: ''
          }]
        },
        formData2: {
          oldPassword: '',
          newPassword: '',
          confirmPassword: ''
        },
        ruleValidate2: {
          oldPassword: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          newPassword: [{
            required: true,
            message: '',
            trigger: 'blur'
          }, {
            pattern: /^[a-zA-Z]\w{5,17}$/,
            message: '6~18',
            trigger: 'blur'
          }],
          confirmPassword: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
        }
      }
    },
    computed: {
      ...mapState({
        userName: state => state.User.userName,
        rsa_exponent : state => state.User.rsa_exponent,
        rsa_modulus : state => state.User.rsa_modulus,
        rsa_passkey : state => state.User.rsa_passkey
      })
    },
    components: {
      tags: tags
    },
    methods: {
      close() {
        Event.$emit('closePage', 'userInfo')
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.modify();
          }
        })
      },
      handleSubmit2(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.modifyPassword();
          }
        })
      },
      modify() {
        const items = this.formData
        UserAPI.modify(items).then((res) => {
          if (res.data.state === 1) {
            this.$Notice.success({
              title: '',
              desc: ''
            });
            Event.$emit('closePage', 'userInfo')
          } else {
            this.$Notice.error({
              title: '',
              desc: res.data.msg
            });
          }
        })
      },
      modifyPassword() {
        if (this.formData2.newPassword === this.formData2.oldPassword) {
          this.$Notice.error({
            title: '',
          });
          return;
        }
        if (this.formData2.confirmPassword !== this.formData2.newPassword) {
          this.$Notice.error({
            title: '',
          });
          return;
        }
        debugger
        let key = parseInt(Math.random()*100000);
        let oldPassword = this.formData2.oldPassword.split("").reverse().join("");
        oldPassword = this.encryptRSA(oldPassword);
        let newPassword = this.formData2.newPassword.split("").reverse().join("");
        newPassword = this.encryptRSA(newPassword);
        let params = {
          'oldPassword':this.rsa_passkey+""+key+""+oldPassword,
          'newPassword':this.rsa_passkey+""+key+""+newPassword,
          'key':key
        };
        UserAPI.modifyPassword(params).then((res) => {
          if (res.data.state === 1) {
            this.$Notice.success({
              title: '',
              desc: ''
            });
            Event.$emit('closePage', 'userInfo');
          } else {
            this.$Notice.error({
              title: '',
              desc: res.data.msg
            })
          }
        });
      },
      encryptRSA (password) {
        var key = window.RSAUtils.getKeyPair(this.rsa_exponent, '', this.rsa_modulus);
        return window.RSAUtils.encryptedString(key, password);
      },
      query() {
        UserAPI.getByName(this.userName).then((res) => {
          if (res.data.state === 1) {
            this.formData = res.data.data
          } else {
            this.$Notice.error({
              title: '',
              desc: res.data.msg
            })
          }
        })
      },
      handleSuccess(res, file) {
        file.url = ''
        file.name = '7eb99afb9d5f317c912f08b5212fd69a'
      },
      handleFormatError(file) {
        this.$Notice.warning({
          title: 'The file format is incorrect',
          desc: 'File format of ' + file.name + ' is incorrect, please select jpg or png.'
        });
      },
      handleMaxSize(file) {
        this.$Notice.warning({
          title: 'Exceeding file size limit',
          desc: 'File  ' + file.name + ' is too large, no more than 2M.'
        })
      },
      handleBeforeUpload() {

      }
    },
    mounted() {
      this.query();
    },
    created() {
      // this.url = UserAPI.uploadFiles()

    }
  }
</script>



