<style scoped>
</style>
<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap"></div>
      <fdcform ref="formData" :model="formData"  :label-width="100" :rules="ruleValidate" >
        <row type="flex" justify="space-around">
          <fdccol span = '7'>
            <formitem label="" prop="code">
              <fdcinput v-model="formData.code" placeholder="" >
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="" prop="name">
              <fdcinput v-model="formData.name" placeholder="" >
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="" prop="loginName">
              <fdcinput v-model="formData.loginName" placeholder="" >
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '7'>
            <formitem label="" prop="type">
              <fdcselect v-model="formData.type" placeholder="" class="search-input">
                <fdcoption v-for="item in enumSelect['EMPLOYEE_TYPE']" :value="item.enumCode" :key="item.enumCode">
                  {{ item.enumValue }}
                </fdcoption>
              </fdcselect>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="" v-show = "false" prop="locationId">
              <fdcinput v-model="formData.locationId" placeholder="" >
              </fdcinput>
            </formitem>
            <formitem label="" prop="locationName">
              <fdcinput v-model="formData.locationName" readonly placeholder="" >
                <fdcbutton slot="append" icon="ios-search" @click="handleLocationModal"></fdcbutton>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="" prop="telephone">
              <fdcinput v-model="formData.telephone" placeholder="" >
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '7'>
            <formitem label="" prop="remark">
              <fdcinput v-model="formData.remark" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="15"></fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '7'>
            <formitem>
              <fdcbutton type="primary" @click="handleSubmit('formData')"></fdcbutton>
              <fdcbutton type="ghost" @click="close('addEmployee')" class="buttonClass"></fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
    <modal v-model="locationShow" :width="800" title="" @on-ok="ok" @on-cancel="cancel">
      <location ></location>
    </modal>
  </div>
</template>

<script>
  import DoOrderAPI from './api/DoOrderAPI'
  import Event from '@/utility/Event.js'
  import location from '@/components/modal/location.vue'
  import {mapState} from 'vuex'

  export default {
    name: 'addEmployee',
    data () {
      return {
        locationShow: false,
        selectValue: [],
        location: {
          id: '',
          name: ''
        },
        formData: {
          loginName: '',
          code:'',
          name: '',
          type: '',
          telephone: '',
          remark: '',
          locationId: '',
          locationName: ''
        },
        ruleValidate: {
          code: [{
            required: true,
            message: '',
            trigger: 'blur'
          },{
            max:17,
            message: '',
            trigger: 'blur'
          }],
          loginName: [{
            required: true,
            message: '',
            trigger: 'blur'
          },{
            max:20,
            message: '',
            trigger: 'blur'
          },{
            pattern: /^[a-z,A-Z,0-9]+$/,
            message: '',
            trigger: 'blur'
          }],
          name: [{
            required: true,
            message: '',
            trigger: 'blur'
          },{
            max:16,
            message: '',
            trigger: 'blur'
          }],
          type: [{
            required: true,
            type: 'number',
            message: '',
            trigger: 'change'
          }],
          locationName: [{
            required: true,
            message: '',
            trigger: 'change'
          }],
          telephone: [{
            max:20,
            message: '',
            trigger: 'blur'
          },{
            pattern: /^[0-9,'\-']+$/,
            message: '',
            trigger: 'blur'
          }],
          remark: [{
            type: 'string',
            trigger: 'blur'
          },{
            max:66,
            message: '',
            trigger: 'blur'
          }]
        }
      }
    },
    computed: {
      ...mapState({
        enumSelect: state => state.Enum.enumSelect
      })
    },
    components: {
      location: location
    },
    methods: {
      ok () {
        this.formData.locationId = this.location.id
        this.formData.locationName = this.location.name
        Event.$emit('clearSelect', null)
      },
      cancel () {
        this.locationShow = false
        Event.$emit('clearSelect', null)
      },
      close (router) {
        Event.$emit('closePage', router)
      },
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.entry()
          }
        })
      },
      handleLocationModal() {
        this.locationShow = true
        Event.$emit('queryLocation', null)
      },
      entry () {
        EmployeeAPI.entry(this.formData).then((res) => {
          if (res.data.state === 1) {
            this.$Notice.success({
              title: '',
              desc: ''
            })
            Event.$emit('closePage', 'addEmployee')
            Event.$emit('refreshList', null)
          } else {
            this.$Notice.error({
              title: '',
              desc: res.data.msg
            })
          }
        })
      },
      handleReset (name) {
        this.$refs[name].resetFields()
      },
      changePage () {
      },
      selectLocation (rowData) {
        this.location = rowData
      }
    },
    mounted () {
      Event.$on('selectLocation', obj => this.selectLocation(obj))
    }
  }
</script>



