<style scoped>
</style>
<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap"></div>
      <fdcform ref="formData" :model="formData"  :label-width="100" >
        <row type="flex" justify="space-around">
          <fdccol span = '5'>
            <formitem label="" prop="code">
              <fdcinput v-model="formData.code" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="" prop="customerCode">
              <fdcinput v-model="formData.customerCode" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="3PL" prop="tplCode">
              <fdcinput v-model="formData.tplCode" readonly >
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="" prop="customerName">
              <fdcinput v-model="formData.customerName" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '5'>
            <formitem label="" prop="orderTime">
              <fdcinput v-model="formData.orderTime" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="" prop="payMode">
              <fdcinput v-model="formData.payMode" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="" prop="amountReceivable">
              <fdcinput v-model="formData.amountReceivable" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="" prop="type">
              <fdcinput v-model="formData.type" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <div class="module-split-wrap"></div>
        <row type="flex" justify="space-around">
          <fdccol span = '5'>
            <formitem label="" prop="receiver">
              <fdcinput v-model="formData.receiver" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="" prop="receiverTelephone">
              <fdcinput v-model="formData.receiverTelephone" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '11'>
            <formitem label="" prop="deliveryTime">
              <fdcinput v-model="formData.deliveryTime" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '5'>
            <formitem label="" prop="toProvince">
              <fdcinput v-model="formData.toProvince" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="" prop="toCity">
              <fdcinput v-model="formData.toCity" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="" prop="toDistrict">
              <fdcinput v-model="formData.toDistrict" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <!--<formitem label="" prop="toAreaName">-->
              <!--<fdcinput v-model="formData.toAreaName" >-->
              <!--</fdcinput>-->
            <!--</formitem>-->
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '23'>
            <formitem label="" prop="receiverAddress">
              <fdcinput v-model="formData.receiverAddress" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <div class="module-split-wrap"></div>
        <row type="flex" justify="space-around">
          <fdccol span = '5'>
            <formitem label="" prop="carrierName">
              <fdcinput v-model="formData.carrierName" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="" prop="deliveryMode">
              <fdcinput v-model="formData.deliveryMode" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="" prop="warehouseName">
              <fdcinput v-model="formData.warehouseName" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <!--<formitem label="" prop="pickupState">-->
              <!--<fdcinput v-model="formData.pickupState" >-->
              <!--</fdcinput>-->
            <!--</formitem>-->
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '5'>
            <formitem label="" prop="siteName">
              <fdcinput v-model="formData.siteName" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="" prop="state">
              <fdcinput v-model="formData.state" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="" prop="returnTime">
              <fdcinput v-model="formData.returnTime" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="5"></fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '23'>
            <formitem label="" prop="remark">
              <fdcinput v-model="formData.remark" readonly >
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <div class="module-split-wrap"></div>
        <div >
          <tabs :value="tabName" type="card">
            <tabpane label="" name="doPackage">
              <div class="table-box">
                <fdctable ref="table" border size="small" :data="tableData" :columns="tableColumns" stripe></fdctable>
                <div class="page-box">
                  <Page placement="top" :page-size="tableCondition.pageNumber" :total="total" :current.sync="tableCondition.pageNo"  @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator show-sizer></Page>
                </div>
              </div>
            </tabpane>
            <tabpane label="" name="doGoods">
            </tabpane>
            <tabpane label="" name="doPayment">
            </tabpane>
            <tabpane label="" name="doShipment">
            </tabpane>
          </tabs>
        </div>
      </fdcform>
    </div>
  </div>
</template>

<script>
  import DoOrderAPI from './api/DoOrderAPI'
  import DoPackageAPI from '../doPackage/api/DoPackageAPI'
  import Event from '@/utility/Event.js'
  import location from '@/components/modal/location.vue'
  import {mapState} from 'vuex'

  export default {
    name: 'doOrderDetail',
    data () {
      return {
        locationShow: false,
        selectValue: [],
        total: 0,
        tabName: 'doPackage',
        location: {
          id: '',
          name: ''
        },
        formData: {
          code: '',
          customerCode:'',
          tplCode: '',
          customerName: '',
          orderTime: '',
          payMode: '',
          amountReceivable:'',
          receiverTelephone: '',
          deliveryTime: '',
          toProvince:'',
          toCity: '',
          toDistrict:'',
          toAreaName:'',
          receiverAddress:''
        },
        tableColumns:[
          {
            title: ' ',
            type: 'index2',
            width: 60,
            align: 'center',
            render: (h, params) => {
              return h('span', params.index + (this.formData.pageNo - 1) * this.formData.pageNumber + 1)
            }
          },
          {
            title: '',
            key: 'code',
            align: 'center'
          },
          {
            title: '',
            key: 'currentLocationName',
            align: 'center'
          },
          {
            title: '(kg)',
            key: 'weight',
            align: 'center'
          },
          {
            title: '(cm)',
            key: 'code',
            align: 'center'
          },
          {
            title: '',
            key: 'weigher',
            align: 'center'
          },
          {
            title: '',
            key: 'weightTime',
            align: 'center'
          },
          {
            title: '',
            key: 'createdTime',
            align: 'center'
          },
        ],
        tableData: [],
        tableCondition:{
          pageNumber: 10,
          pageNo:1
        }
      }
    },
    computed: {
      ...mapState({
        enumSelect: state => state.Enum.enumSelect
      })
    },
    components: {
      location: location
    },
    methods: {
      close (router) {
        Event.$emit('closePage', router)
      },
      changePageNo(value) {
        this.formData.pageNo = value
        this.query()
      },
      changePageSize(value) {
        this.formData.pageNo = 1
        this.formData.pageNumber = value
        this.query()
      },
      query () {
        DoOrderAPI.getById(this.$route.query.paramId).then((res) => {
          if (res.data) {
            this.formData = res.data
            this.formData.type = this.sysEnumList['ORDER_TYPE_' + res.data.type]
            this.formData.state = this.sysEnumList['ORDER_STATE_' + res.data.state]
            this.formData.payMode = this.sysEnumList['PAY_MODE_' + res.data.payMode]
            this.formData.deliveryMode = this.sysEnumList['DELIVERY_MODE_' + res.data.deliveryMode]
          } else {
            this.$Notice.error({
              title: '',
              desc: res.data.msg
            })
          }
        })
        DoPackageAPI.query(this.tableCondition).then((res) => {
          this.tableData = []
          let state = res.data.state
          if (state === 1 && res.data.data) {
            let dataList = res.data.data.list
            if (dataList !== null) {
              this.total = res.data.data.total
            }
            this.tableData.push(...dataList)
          }
        })
      }
    },
    computed: {
      ...mapState({
        sysEnumList: state => state.Enum.enumList
      })
    },
    mounted () {
      Event.$emit('addEnum', {typeList:['ORDER_STATE','ORDER_TYPE','DELIVERY_MODE','PAY_MODE']})
      this.query()
    }
  }
</script>



