<template>
  <div>
    <div class="form-box-content" style="padding-top: 15px;padding-bottom: 15px">
      <fdcform ref="form" :model="formData" :rules="paramValidate" :label-width="120">
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem :label="''" prop="unityCode">
              <fdcinput v-model="formData.unityCode" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem :label="''" prop="name">
              <fdcinput v-model="formData.name" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem :label="'IP'" prop="ipWhiteList">
              <fdcinput v-model="formData.ipWhiteList" placeholder="IP">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12" >
            <formitem :label="'IP'" prop="ipBlackList">
              <fdcinput v-model="formData.ipBlackList" placeholder="IP">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12" >
            <formitem :label="''" prop="linkman">
              <fdcinput v-model="formData.linkman" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12" >
            <formitem :label="''" prop="linkmanEmail" >
              <fdcinput v-model="formData.linkmanEmail" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem :label="''" prop="linkmanMobile">
              <fdcinput v-model="formData.linkmanMobile" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12" >
            <formitem>
            <fdcbutton type="primary" @click="handleSubmit('form')"></fdcbutton>
            <fdcbutton type="ghost" @click="handleReset('form')" class="buttonClass"></fdcbutton>
            <fdcbutton type="ghost" @click="close('apiConsumerAdd')" class="buttonClass"></fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
  </div>
</template>

<script>
import Event from '@/utility/Event.js'
import ConsumerAPI from './api/ConsumerAPI'
import {mapState, mapAction} from 'vuex'
export default {
  name: 'apiConsumerAdd',
  data () {
    return {
      paramValidate: {
        unityCode: [{required: true, type: 'string', message: '', trigger: 'blur'}],
        name: [{required: true, type: 'string', message: '', trigger: 'blur'}],
        linkman: [{required: true, type: 'string', message: '', trigger: 'blur'}],
        linkmanEmail: [{required: true, type: 'string', message: '', trigger: 'blur'}],
        linkmanMobile: [{required: true, type: 'string', message: '', trigger: 'blur'}]
      },
      formData: {
        unityCode: '',
        name: '',
        linkman: '',
        linkmanEmail: '',
        linkmanMobile: '',
        ipWhiteList: '',
        ipBlackList: ''
      }
    }
  },
  methods: {
    handleSubmit (name) {
      this.add()
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    add () {
      ConsumerAPI.add(this.formData).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '',
            desc: ''
          })
          this.close('apiConsumerAdd')
        } else {
          this.$Notice.error({
            title: '',
            desc: res.data.msg
          })
        }
      })
    },
    close (router) {
      Event.$emit('closePage', router)
      Event.$once('refreshConsumerList', null)
    }
  },
  components: {}
}
</script>



