<style scoped>
</style>
<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap"></div>
      <fdcform ref="formData" :model="formData"  :label-width="80" :rules="ruleValidate" >
        <row type="flex" justify="space-around">
          <fdccol span = '7'>
            <formitem label="" prop="loginName">
              <fdcinput v-model="formData.loginName" disabled placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="" prop="name">
              <fdcinput v-model="formData.name" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="" prop="email">
              <fdcinput v-model="formData.email" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '7'>
            <formitem label="" prop="contactInfo">
              <fdcinput v-model="formData.contactInfo" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="" prop="department">
              <fdcinput v-model="formData.department" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="" prop="idCard">
              <fdcinput v-model="formData.idCard" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '7'>
            <formitem label="" v-show = "false" prop="companyId">
              <fdcinput v-model="formData.companyId" placeholder="">
              </fdcinput>
            </formitem>
            <formitem label="" >
              <fdcinput v-model="formData.companyName" placeholder="">
                <fdcbutton slot="append" icon="ios-search"  :disabled="companyButton"  @click="handleCompanyModal"></fdcbutton>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="15"></fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '7'>
            <formitem>
              <fdcbutton type="primary" @click="handleSubmit('formData')"></fdcbutton>
              <fdcbutton type="ghost" @click="close('modifyUser')" class="buttonClass"></fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
    <modal v-model="companyShow" :width="800" title="" @on-ok="ok" @on-cancel="cancel">
      <company ></company>
    </modal>
  </div>
</template>

<script>
import UserAPI from './api/UserAPI'
import Event from '@/utility/Event.js'
import company from '@/components/modal/company.vue'

export default {
  name: 'modifyUser',
  data () {
    return {
      companyButton: true,
      companyShow: false,
      company: {
        id: '',
        name: ''
      },
      formData: {
        id: '',
        loginName: '',
        name: '',
        email: '',
        contactInfo: '',
        userType: '',
        department: '',
        idCard: '',
        companyId: '',
        companyName: ''
      },
      ruleValidate: {

        loginName: [{
          required: true,
          message: '',
          trigger: 'blur'
        },{
          max:20,
          message: '',
          trigger: 'blur'
        },{
          pattern: /^[a-z,A-Z,0-9]+$/,
          message: '',
          trigger: 'blur'
        }],
        name: [{
          required: true,
          message: '',
          trigger: 'blur'
        },{
          max:16,
          message: '',
          trigger: 'blur'
        }],
        email: [{
          type: 'email',
          message: '',
          trigger: 'blur'
        },{
          max:50,
          message: '',
          trigger: 'blur'
        }],
        contactInfo: [{
          type: 'string',
          trigger: 'blur'
        },{
          max:66,
          message: '',
          trigger: 'blur'
        }],
        department: [{
          type: 'string',
          trigger: 'blur'
        },{
          max:10,
          message: '',
          trigger: 'blur'
        }],
        idCard: [{
          pattern: /^[0-9,'X']{15,18}$/,
          message: ''
        }],
        companyName: [{
          required: true,
          message: '',
          trigger: 'blur'
        }]
      }
    }
  },
  computed: {
  },
  components: {
    company: company
  },
  methods: {
    ok () {
      this.formData.companyId = this.company.id
      this.formData.companyName = this.company.name
    },
    cancel () {
      this.companyShow = false
    },
    close (router) {
      Event.$emit('closePage', router)
    },
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.modify()
        }
      })
    },
    modify () {
      UserAPI.modify(this.formData).then((res) => {
        if (res.data.state === 1) {
          this.$Notice.success({
            title: '',
            desc: ''
          })
          Event.$emit('closePage', 'modifyUser')
          Event.$emit('refreshList', null)
        } else {
          this.$Notice.error({
            title: '',
            desc: res.data.msg
          })
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    changePage () {
    },
    selectCompany (rowData) {
      if (rowData !== null) {
        this.company = rowData
      }
    },
    query () {
      UserAPI.getById(this.$route.query.paramId).then((res) => {
        if (res.data) {
          if (res.data.localLevel) {
            this.companyButton = false
          }
          this.formData = res.data
          this.formData.userType = res.data.userType
        } else {
          this.$Notice.error({
            title: '',
            desc: res.data.msg
          })
        }
      })
    },
    handleCompanyModal() {
      this.companyShow = true
      Event.$emit('queryCompany', null)
    }
  },
  created () {
    this.query()
    Event.$on('selectCompany', obj => this.selectCompany(obj))
  }
}
</script>



