<template>
  <div>
    <div class="form-box-content" style="padding-top: 15px;padding-bottom: 15px">
    <fdcform ref="form" :model="formData" :rules="paramValidate" :label-width="120">
      <row type="flex" justify="space-around">
        <fdccol span="12" >
          <formitem :label="''" prop="fullName">
            <fdcinput v-model="formData.fullName" placeholder=""></fdcinput>
          </formitem>
        </fdccol>
      </row>
      <row type="flex" justify="space-around">
        <fdccol span="12" >
          <formitem :label="''" prop="developer">
            <fdcinput v-model="formData.developer" placeholder=""></fdcinput>
          </formitem>
        </fdccol>
      </row>
      <row type="flex" justify="space-around">
        <fdccol span="12" >
          <formitem :label="''" prop="developerMobile">
            <fdcinput v-model="formData.developerMobile" placeholder=""></fdcinput>
          </formitem>
        </fdccol>
      </row>
      <row type="flex" justify="space-around">
        <fdccol span="12" >
          <formitem :label="''" prop="developerEmail">
            <fdcinput v-model="formData.developerEmail" placeholder=""></fdcinput>
          </formitem>
        </fdccol>
      </row>
      <row type="flex" justify="space-around">
        <fdccol span="12" >
          <formitem>
            <fdcbutton type="primary" @click="handleSubmit('form')" ></fdcbutton>
            <fdcbutton type="ghost" @click="handleReset('form')" class="buttonClass"></fdcbutton>
            <fdcbutton type="ghost" @click="close('serviceAdd')" class="buttonClass"></fdcbutton>
          </formitem>
        </fdccol>
      </row>
    </fdcform>
    </div>
  </div>
</template>

<script>
import Event from '@/utility/Event.js'
import ServiceAPI from './api/ServiceAPI'

export default {
  name: 'serviceAdd',
  data () {
    return {
      paramValidate: {
        fullName: [{
          required: true,
          type: 'string',
          message: '',
          trigger: 'blur'
        }],
        developer: [{
          required: true,
          type: 'string',
          message: '',
          trigger: 'blur'
        }],
        developerEmail: [{
          required: true,
          type: 'string',
          message: '',
          trigger: 'blur'
        }],
        developerMobile: [{
          required: true,
          type: 'string',
          message: '',
          trigger: 'blur'
        }]
      },
      formData: {
        fullName: '',
        developer: '',
        developerEmail: '',
        developerMobile: ''
      }
    }
  },
  methods: {
    handleSubmit (name) {
      this.add()
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    add () {
      ServiceAPI.add(this.formData).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '',
            desc: ''
          })
          this.close('serviceAdd')
        } else {
          this.$Notice.error({
            title: '',
            desc: res.data.msg
          })
        }
      })
    },
    close (router) {
      Event.$emit('closePage', router)
      Event.$once('refreshServiceList', null)
    }
  },
  computed: {
  },
  components: {}
}
</script>



