<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap"></div>
      <fdcform ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="100">
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem label="" prop="parentId">
              <fdcselect v-model="formValidate.parentId">
                <fdcoption v-for="item in cityList" :value="item.id" :key="item.id">
                  {{ item.name }}
                </fdcoption>
              </fdcselect>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
        <fdccol span="12">
          <formitem label="" prop="code">
            <fdcinput v-model="formValidate.code" placeholder=""></fdcinput>
          </formitem>
        </fdccol>
        </row>
        <row type="flex" justify="space-around">
        <fdccol span="12">
          <formitem label="" prop="name">
            <fdcinput v-model="formValidate.name" placeholder=""></fdcinput>
          </formitem>
        </fdccol>
        </row>
        <row type="flex" justify="space-around">
        <fdccol span="12">
          <formitem>
            <fdcbutton type="primary" @click="handlerSubmit('formValidate')"></fdcbutton>
            <fdcbutton type="ghost" @click="cancel('addDistrict')" class="buttonClass"></fdcbutton>
          </formitem>
        </fdccol>
        </row>
      </fdcform>
    </div>
  </div>
</template>
<script>
import GeoAPI from './api/GEOAPI'
import Event from '@/utility/Event.js'
export default {
  name: 'addDistrict',
  data: function () {
    return {
      ruleValidate: {
        code: [{
          required: true,
          type: 'string',
          message: '',
          trigger: 'blur'
        }],
        parentId: [{
          required: true,
          type: 'string',
          message: '',
          trigger: 'blur'
        }],
        name: [{
          required: true,
          type: 'string',
          message: '',
          trigger: 'blur'
        }]
      },
      formValidate: {
        type: 3,
        name: '',
        code: '',
        parentId: ''

      },
      cityList: ''
    }
  },
  methods: {
    getCityList: function () {
      GeoAPI.getCityList().then((res) => {
        if (res.data) {
          this.cityList = res.data
        }
      })
    },
    insert: function () {
      GeoAPI.insert(this.formValidate).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '',
            desc: ''
          })
          Event.$emit('closePage', 'addDistrict')
          Event.$emit('refreshDistrictList', null)
        } else {
          this.$Notice.error({
            title: '',
            desc: res.data.msg
          })
        }
      })
    },
    handlerSubmit: function (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.insert()
        }
      })
    },
    cancel: function (router) {
      Event.$emit('closePage', router)
    }
  },
  mounted: function () {
    this.getCityList()
  }
}
</script>



