<style scoped>
</style>
<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap"></div>
      <fdcform ref="formData" :model="formData"  :label-width="120" :rules="ruleValidate">
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="" prop="queueCode">
              <fdcinput v-model="formData.queueCode" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="" prop="queueType">
              <fdcselect v-model="formData.queueType" placeholder="">
                <fdcoption v-for="item in enumSelect['QUEUE_TYPE']" :value="item.enumCode" :key="item.enumCode">
                  {{ item.enumValue }}
                </fdcoption>
              </fdcselect>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="" prop="threadNum">
              <fdcinput v-model="formData.threadNum" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="" prop="protocol">
              <fdcinput v-model="formData.protocol" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="" prop="retryNum">
              <fdcinput v-model="formData.retryNum" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="" prop="maxByte">
              <fdcinput v-model="formData.maxByte" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="" prop="queryNum">
              <fdcinput v-model="formData.queryNum" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="URL" prop="targetUrl">
              <fdcinput v-model="formData.targetUrl" placeholder="URL">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="URL" prop="notifyUrl">
              <fdcinput v-model="formData.notifyUrl" placeholder="URL">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>

        <row type="flex" justify="space-around">
          <fdccol span = '7'>
            <formitem>
              <fdcbutton type="primary" @click="handleSubmit('formData')"></fdcbutton>
              <fdcbutton type="ghost" @click="close" class="buttonClass"></fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
  </div>
</template>

<script>
  import Event from '@/utility/Event.js'
  import company from '@/components/modal/company.vue'
  import {mapState} from 'vuex'
  import SysTaskConfigAPI from "./api/SysTaskConfigAPI";

  export default {
    name: 'addTaskConfig',
    data () {
      return {
        formData: {
          queueCode: '',
          queueType: '',
          threadNum: '',
          protocol: '',
          retryNum: '',
          maxByte: '',
          queryNum: '',
          targetUrl: '',
          notifyUrl: ''
        },
        ruleValidate: {
          queueCode: [{
            type: 'string',
            required: true,
            message: '',
            trigger: 'blur'
          }],
          queueType: [{
            required: true,
            type: 'number',
            message: '',
            trigger: 'change'
          }],
          threadNum: [{
            required: true,
            message: '',
            pattern: /^\d+$/,
            trigger: 'blur'
          }],
          protocol: [{
            required: true,
            type: 'string',
            message: '',
            trigger: 'blur'
          }],
          retryNum: [{
            required: true,
            pattern: /^\d+$/,
            message: '',
            trigger: 'blur'
          }],
          maxByte: [{
            required: true,
            pattern: /^\d+$/,
            message: '',
            trigger: 'blur'
          }],
          queryNum: [{
            required: true,
            pattern: /^\d+$/,
            message: '',
            trigger: 'blur'
          }],
          targetUrl: [{
            required: true,
            type: 'url',
            message: 'URL'
          }],
          notifyUrl: [{
            required: true,
            type: 'url',
            message: 'URL'
          }]
        }
      }
    },
    computed: {
      ...mapState({
        enumSelect: state => state.Enum.enumSelect
      })
    },
    components: {
      company: company
    },
    methods: {
      close () {
        Event.$emit('closePage', 'addTaskConfig')
      },
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.insert()
          }
        })
      },
      insert () {
        SysTaskConfigAPI.insert(this.formData).then((res) => {
          if (res.data.state === 1) {
            this.$Notice.success({
              title: '',
              desc: ''
            })
            Event.$emit('closePage', 'addTaskConfig');
            Event.$emit('refreshList', obj => this.query());
          } else {
            this.$Notice.error({
              title: '',
              desc: res.data.msg
            })
          }
        })
      }
    },
    mounted () {
      Event.$emit('addEnum', {typeList:['QUEUE_TYPE']});
    }
  }
</script>



